* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Fira Sans", sans-serif;
}

.header,
footer {
  text-align: center;
  font-size: 20px;
  padding: 8px;
  width: 100%;
  background-color: rgb(49, 46, 46);
  color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.footer {
  position: fixed;
  bottom: 0;
}

.container {
  font-family: "Fira Sans", sans-serif;
  font-size: 20px;
  text-align: center;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* position: absolute;
  top: 680px;
  left: 40px; */
  color: black;
}

.add-shop {
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
}

.shop-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.hide-shop-container {
  visibility: hidden;
  height: 0;
}

.show-shop-container {
  visibility: visible;
}

.total-items {
  margin: 4px 18px;
  font-weight: bold;
}

.shop-card {
  border-radius: 12px;
  border: 0.5px black solid;
  padding: 5px;
  margin: 10px;
  /* background-color: rgb(231, 229, 229); */
}

.shop-card-header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px 18px;
}

.shop-image {
  width: 40%;
  max-height: 70px;
  margin: 8px;
}

.selected-shop-image {
  width: 40%;
  max-height: 70px;
}

.remove-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-family: "Fira Sans", sans-serif;
  margin: 5px 20px;
}

.remove-shop-container,
.remove-all-items-container {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: 1px black solid;
  max-width: 44%;
  background-color: rgb(240, 69, 69);
  height: 40px;
  padding: 5px;
}

.remove-shop-wording,
.remove-all-items-wording {
  margin-left: 10px;
}

.shop-items-remaining {
  margin: 4px 18px;
  font-weight: bold;
}

.input-container {
  display: flex;
  justify-content: center;
  margin: 5px 0px;
  border-radius: 12px;
}

.list-input {
  width: 75%;
  padding: 5px;
  font-size: 20px;
  text-align: center;
  border: black solid 0.5px;
}

.add-btn {
  padding: 5px;
  font-size: 20px;
  text-align: center;
  background-color: rgb(29, 233, 29);
  font-weight: 800;
  border: black solid 0.5px;
}

.list-container {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: black solid 1px;
  background-color: rgb(231, 229, 229);
  max-width: 88%;
  margin-bottom: 5px;
  margin-top: 5px;
  padding: 5px;
  border-radius: 5px;
}

.list-items,
.react-icons {
  font-size: 20px;
  margin-right: 10px;
  color: black;
}
